html,
body {
	height: 100%;
}
body {
	font-family: 'Open Sans', sans-serif;
	font-size: 16px;
	line-height: 27px;
	font-weight: 400;
	color: default;
	overflow-x: hidden;
	min-width: 320px;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

.main {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}

.container-main{
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;

}

.container-fluid{
	max-width: 1900px;
	margin: 0 auto;
	padding: 0 10px;
}

.info-email{
	font-weight: 600;
	font-size: 14px;
	color: $title;
	margin-bottom: 22px;
	padding-left: 30px;
	display: inline-block;
	position: relative;
	&::before{
		content: "";
		background-image: url(../img/envelope.svg);
		position: absolute;
		left: 0;
		width: 16px;
		height: 12px;
		top: 8px;
	}
}



